import React, {
  createContext,
  useContext,
  useState,
  useEffect,
  ReactNode,
} from 'react'

interface User {
  id: string
  name: string
  email: string
}

interface AuthContextData {
  user: User | null
  isLoading: boolean
  isAuthenticated: boolean
  login: (
    email: string,
    password: string
  ) => Promise<{ success: boolean; error?: string }>
  register: (
    name: string,
    email: string,
    password: string,
    confirmPassword: string
  ) => Promise<{ success: boolean; error?: string }>
  logout: () => void
}

const AuthContext = createContext<AuthContextData | null>(null)

export const AuthProvider: React.FC<{ children: ReactNode }> = ({
  children,
}) => {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [isAuthenticated, setIsAuthenticated] = useState(false)

  // VITE_API_URL deve incluir j o `/api`, ex: https://www.betforbes.com/api
  const API = import.meta.env.VITE_API_URL || ''

  // Restaura sesso
  useEffect(() => {
    ;(async () => {
      const token = localStorage.getItem('authToken')
      if (token) {
        try {
          const res = await fetch(`${API}/auth/me`, {
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${token}`,
            },
            credentials: 'include',
          })
          const body = await res.json()
          if (res.ok && body.success) {
            setUser(body.data.user)
            setIsAuthenticated(true)
          } else {
            localStorage.removeItem('authToken')
          }
        } catch {
          localStorage.removeItem('authToken')
        }
      }
      setIsLoading(false)
    })()
  }, [API])

  const login = async (email: string, password: string) => {
    setIsLoading(true)
    try {
      const res = await fetch(`${API}/auth/login`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify({ email, password }),
      })
      const body = await res.json()

      if (!res.ok || body.success === false) {
        setIsLoading(false)
        return { success: false, error: body.message || 'Credenciais invlidas' }
      }

      const token = body.data.tokens.accessToken
      const u: User = body.data.user

      localStorage.setItem('authToken', token)
      setUser(u)
      setIsAuthenticated(true)
      setIsLoading(false)
      return { success: true }
    } catch (err: any) {
      setIsLoading(false)
      return { success: false, error: err.message || 'Erro de rede' }
    }
  }

  const register = async (
    name: string,
    email: string,
    password: string,
    confirmPassword: string
  ) => {
    setIsLoading(true)
    try {
      const res = await fetch(`${API}/auth/register`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify({ name, email, password, confirmPassword }),
      })
      const body = await res.json()

      if (!res.ok || body.success === false) {
        setIsLoading(false)
        return { success: false, error: body.message || 'Dados invlidos' }
      }

      setIsLoading(false)
      return { success: true }
    } catch (err: any) {
      setIsLoading(false)
      return { success: false, error: err.message || 'Erro de rede' }
    }
  }

  const logout = () => {
    localStorage.removeItem('authToken')
    setUser(null)
    setIsAuthenticated(false)
  }

  return (
    <AuthContext.Provider
      value={{
        user,
        isLoading,
        isAuthenticated,
        login,
        register,
        logout,
      }}
    >
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const ctx = useContext(AuthContext)
  if (!ctx) throw new Error('useAuth must be inside AuthProvider')
  return ctx
}
