import React from 'react';
import { useAuth } from '../contexts/AuthContext';
import { Navigate } from 'react-router-dom';

const AdminPage: React.FC = () => {
  const { user, isLoading } = useAuth();

  // Enquanto carrega os dados do usurio
  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <p>Verificando acesso...</p>
      </div>
    );
  }

  // Verifica se o usurio est logado E se  admin
  // A rota /admin j deve ser protegida por PrivateRoute, mas adicionamos uma verificao extra aqui
  // O PrivateRoute pode verificar apenas se est logado, e aqui verificamos o 'isAdmin'
  // Ou o PrivateRoute pode receber um parmetro para verificar 'isAdmin' diretamente.
  // Assumindo que o PrivateRoute j garante que 'user' no  null, mas verificamos 'isAdmin'.
  if (!user || !user.isAdmin) {
    // Se no for admin, redireciona para o painel normal ou login
    // Idealmente, o PrivateRoute j faria isso, mas  uma segurana.
    console.warn('Acesso no autorizado  pgina de admin.');
    return <Navigate to="/painel" replace />;
    // Ou poderia mostrar uma mensagem de acesso negado:
    // return (
    //   <div className="container mx-auto p-4">
    //     <h1 className="text-2xl font-bold text-red-600">Acesso Negado</h1>
    //     <p>Voc no tem permisso para acessar esta pgina.</p>
    //   </div>
    // );
  }

  // Se chegou aqui,  admin
  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Painel Administrativo</h1>
      <p>Bem-vindo(a), Administrador(a) <span className="font-semibold">{user.name}</span>!</p>
      <p>Esta rea  restrita e contm funcionalidades administrativas.</p>
      {/* TODO: Adicionar componentes e funcionalidades especficas do admin */}

      <div className="mt-8 border p-4 rounded-lg shadow">
        <h2 className="text-lg font-semibold mb-2">Gerenciamento de Usurios (Mock)</h2>
        <p>Aqui voc poder gerenciar os usurios da plataforma.</p>
        <button className="mt-2 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600">
          Ver Usurios (Mock)
        </button>
      </div>
    </div>
  );
};

export default AdminPage;

